// ** Path utilities
export class Path {

    // ** transform dot notation to slash
    static dotToSlash(path:string):string {
        if(path.indexOf('.')!=-1) { return path.split('.').join('/') }
        else { return path }
    }

    // ** parse context to valid Signal K path
    static contextToPath(context:string):string {
        let res= (context=='self' ) ? 'vessels.self': context;
        return res.split('.').join('/');
    }    

}

// ** Message templates **
export class Message {
   
    // ** return UPDATES message object
    static updates() { 
        // array values= { values: [ {path: xx, value: xx } ] }
        return { 
            context: null,
            updates: [] 
        }
    }
    // ** return SUBSCRIBE message object
    static subscribe() { 
        /* array values= {
            "path": "path.to.key",
            "period": 1000,
            "format": "delta",
            "policy": "ideal",
            "minPeriod": 200
            } */
        return { 
            context: null,
            subscribe: [] 
        }
    }    
    // ** return UNSUBSCRIBE message object
    static unsubscribe() { 
        // array values= { "path": "path.to.key" }
        return { 
            context: null,
            unsubscribe: [] 
        }
    }  
    // ** return REQUEST message object
    static request() { 
        return { 
            requestId: null
        }
    }           

}

